import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/Subject';
import { Observable } from 'rxjs/Observable';

/**
 * @hidden
 */
export interface Item {
    options: any;
}

/**
 * @hidden
 */
export class ItemChange {
    constructor(public sender: Item, public options: any) {
    }
}

/**
 * @hidden
 */
@Injectable()
export class CollectionService {
  public onItemChange$: Observable<ItemChange>;

  private source: Subject<ItemChange>;

  constructor() {
    this.source = new Subject<ItemChange>();
    this.onItemChange$ = this.source.asObservable();
  }

  public notify(change: ItemChange): void {
    this.source.next(change);
  }
}



